import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import * as moment from 'moment';
import { ExportToExcelService } from 'src/app/core/services/common/export-to-excel.service';
import { CustomerService } from 'src/app/core/services/customer/customer.service';
import { PagerService } from 'src/app/core/services/pagination/pager.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-manage-customer',
  templateUrl: './manage-customer.component.html',
  styleUrls: ['./manage-customer.component.scss'],
})
export class ManageCustomerComponent implements OnInit {
  arrCustomer: any = [];
  arrAllCustomers: any = [];
  // pagination
  pager: any = {};
  intTotalCount = 0;
  intPageLimit = 50;
  intSkipCount = 0;
  fetchLoading = false;

  myForm: FormGroup;

  constructor(
    private customerService: CustomerService,
    private formBuilder: FormBuilder,
    private exportToExcelService: ExportToExcelService,
    private pageServiceObj: PagerService
  ) {
    this.myForm = this.formBuilder.group({
      customer: [null],
      fromDate: [''],
      toDate: [''],
    });
  }

  ngOnInit() {
    this.getAllCustomer();
    this.getAllCustomerAll();
  }

  setPage(page) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.pager = this.pageServiceObj.getPager(
      this.intTotalCount,
      page,
      this.intPageLimit
    );
    this.getAllCustomer();
  }

  getAllCustomerAll() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      fkCustomerId: '',
      strName: '',
      cust_Code: '',
      fromDate: '',
      endDate: '',
      intSkipCount: '0',
      intPageLimit: '1000000',
    };
    this.customerService.getAllCustomer(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrAllCustomers = res.data.map((d) => {
          return {
            ...d,
            displayName: d.cust_Code + ' - ' + d.strName,
          };
        });
      } else {
        this.arrAllCustomers = [];
      }
    });
  }

  getFromCount() {
    return this.intPageLimit * (this.pager.currentPage - 1) + 1;
  }

  getToCount() {
    return (
      this.intPageLimit * (this.pager.currentPage - 1) + this.arrCustomer.length
    );
  }
  getAllCustomer() {
    let skipCount = this.intSkipCount;
    if (this.pager.intSkipCount) {
      skipCount = this.pager.intSkipCount;
    }
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: skipCount,
      intPageLimit: this.intPageLimit,
      fkCustomerId: '',
      cust_Code: this.myForm.value.customer,
      fromDate: this.myForm.value.fromDate,
      endDate: this.myForm.value.toDate,
    };

    this.fetchLoading = true;
    this.customerService.getAllCustomer(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrCustomer = res.data;

        this.intTotalCount = res.count;

        this.pager = this.pageServiceObj.getPager(
          this.intTotalCount,
          this.pager.currentPage,
          this.intPageLimit
        );
      } else {
        this.arrCustomer = [];
        // alert(res.message);
      }
    });
  }

  downloadExcel() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1000000,
      fkCustomerId: '',
      cust_Code: this.myForm.value.customer,
      fromDate: this.myForm.value.fromDate,
      endDate: this.myForm.value.toDate,
    };

    this.fetchLoading = true;
    this.customerService.getAllCustomer(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        let jsonData: any = [];

        res.data.forEach((e) => {
          const cost_Details = e.cost_Details
            .map((expense) => `${expense.name}: ${expense.amount}`)
            .join(', ');

          let obj: any = {
            'Customer Code': e.cust_Code,
            'Customer Name': e.strName,
            'Supplier Name': e.supplier_Name,
            Email: e.strEmail,
            Phone: e.phone,
            Country: e.country,
            'Amount per Person': e.per_Person,
            'Number of Person': e.no_Of_Persons,
            VAT: e.vat,
            'Total Amount': e.Total_Amount,
            'Paid Amount': e.Total_Paid_Amount,
            'Balance Amount': e.Total_Amount - e.Total_Paid_Amount,
            'Cost Price': e.cost_Price,
            'Refund Amount': e.Refund_Amount,
            'G-Voucher Credit Amount': e.Service_Credit_Amount,
            'G-Voucher Debit Amount': e.Service_Debit_Amount,
            'Profit Price': e.profit_Price,
            Package: e.strPackage,
            'Travel Date': moment(e.travel_Date).format('DD-MM-YYYY'),
            Description: e.strDescription,
            'Cost Details': cost_Details,
            'Created Admin': e.createdAdminName,
            'Created On': moment(e.createdAt).format('DD-MM-YYYY'),
            'Updated Admin': e.updatedAdminName,
            'Updated On': moment(e.updatedAt).format('DD-MM-YYYY'),
          };
          jsonData.push(obj);
        });

        this.exportToExcelService.exportAsExcelFile(jsonData, 'Customers ');
      }
    });
  }

  deleteCustomer(id, fkSupplierId) {
    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#d33',
      reverseButtons: true,
      confirmButtonText: 'Yes, delete it!',
    }).then((result) => {
      if (result.isConfirmed) {
        const obj = {
          strLoginUserId: localStorage.getItem('userId'),
          fkCustomerId: id,
          fkSupplierId: fkSupplierId,
        };

        // console.log('obj', obj);
        this.customerService.deleteCustomer(obj).subscribe((res) => {
          if (res.success) {
            Swal.fire({
              title: 'Deleted!',
              text: 'Your file has been deleted.',
              icon: 'success',
            });
            this.getAllCustomer();
          } else {
            // alert(res.message);
          }
        });
      }
    });
  }
}
